"use client";

import { useState } from "react";
import { CardContent, CardHeader, CardTitle } from "@/components/ui/card";

// TypeScript interfaces for component state and data
interface EmotionDataPoint {
  timestamp: number;
  emotions: {
    joy: number;
    sadness: number;
    anger: number;
    fear: number;
    surprise: number;
    disgust: number;
    contempt: number;
  };
  confidence: number;
  source: 'facial' | 'vocal';
}

interface CheckinState {
  phase: 'idle' | 'recording' | 'finished';
  timeRemaining: number;
  webcamReady: boolean;
  emotionData: EmotionDataPoint[];
  humeWebSocket: WebSocket | null;
  eviWebSocket: WebSocket | null;
}

interface PatientCheckinProps {
  // Future props for configuration
  className?: string;
}

export function PatientCheckin({}: PatientCheckinProps) {
  // Component state management for idle/recording/finished phases
  const [checkinState] = useState<CheckinState>({
    phase: 'idle',
    timeRemaining: 60, // 60-second timer
    webcamReady: false,
    emotionData: [],
    humeWebSocket: null,
    eviWebSocket: null,
  });

  // Basic Card layout structure - using CardContent since Card wrapper is in parent
  return (
    <>
      <CardHeader>
        <CardTitle className="text-center text-neutral-900">
          Patient Check-in
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-6">
        {/* Content will be implemented in future tasks based on phase */}
        <div className="text-center">
          <p className="text-neutral-600 mb-4">
            Current Phase: {checkinState.phase}
          </p>
          <p className="text-neutral-600 mb-4">
            Time Remaining: {checkinState.timeRemaining}s
          </p>
          <p className="text-neutral-600 mb-4">
            Webcam Ready: {checkinState.webcamReady ? 'Yes' : 'No'}
          </p>
          <p className="text-neutral-600">
            Component foundation ready for implementation.
          </p>
        </div>
      </CardContent>
    </>
  );
}